import React from 'react';
import { ModalRoot, ModalCard, InfoRow, FormLayout, Input,} from '@vkontakte/vkui';

const ModalShopBuyItem = ({iconItem, nameItem, ratingItem, onChangeHandlerAmount, 
  timeItem, buyItem, targetIdShop, amount, setActiveModal }) => {


    return (
      <ModalRoot activeModal="info">
      <ModalCard
        id="info"
        onClose={() => setActiveModal(null)}
        icon={iconItem}
        header={nameItem}
        caption={
          <>
            {targetIdShop >= 1 && targetIdShop <= 3 ? (
              <>
                <InfoRow>
                  {nameItem} - при использовании прибавляет +{ratingItem} к
                  рейтингу.
                </InfoRow>
                <FormLayout>
                  <Input
                    top={`Укажите кол-во ${nameItem}`}
                    placeholder="1"
                    onChange={onChangeHandlerAmount}
                    type="number"
                  />
                </FormLayout>
              </>
            ) : nameItem === "Отпуск" ? (
              <>
                <InfoRow>
                  {nameItem} - предоставляет вам отпуск на {timeItem}, в
                  течение этого времни, никто не может использовать на вас
                  'смирительную рубашку' или 'одиночную камеру'
                </InfoRow>
                <FormLayout>
                  <Input
                    top={`Укажите кол-во ${nameItem}`}
                    placeholder="1"
                    onChange={onChangeHandlerAmount}
                    type="number"
                  />
                </FormLayout>
              </>
            ) : nameItem === "Смирительная рубашка" ? (
              <>
                <InfoRow>
                  {nameItem} - отнимает {ratingItem} рейтинга у любого игрока
                  ДУРКИ
                </InfoRow>
                <FormLayout>
                  <Input
                    top={`Укажите кол-во ${nameItem}`}
                    placeholder="1"
                    onChange={onChangeHandlerAmount}
                    type="number"
                  />
                </FormLayout>
              </>
            ) : nameItem === "Одиночная камера" ? (
              <>
                <InfoRow>
                  {nameItem} - отнимает {ratingItem} рейтинга у любого игрока
                  ДУРКИ.
                </InfoRow>
                <FormLayout>
                  <Input
                    top={`Укажите кол-во ${nameItem}`}
                    placeholder="1"
                    onChange={onChangeHandlerAmount}
                    type="number"
                  />
                </FormLayout>
              </>
            ) : (
              ""
            )}
          </>
        }
        actions={[
          {
            title: "Купить",
            mode: "commerce",
            action: () => {
              buyItem(targetIdShop, amount);
            },
          },
        ]}
      ></ModalCard>
    </ModalRoot>
    );
};

export { ModalShopBuyItem };

