  durkaCoins,
  shisoids,
  openTransfer,
  snackbar,
  rating,
  scheme,
  openBuyDurkaCoins,
  networkStatus,
  updateConnection,
  socketReconnection,
  socketReconnect,
  connectTwoDevices,
  stub,
  online,
  setPopout,
  openAlertPay,
  setActivePanel,
