import React from "react";
import {
  ModalRoot,
  ModalCard,
  FormLayout,
  Input,
  Avatar,
  InfoRow,
  FormLayoutGroup
} from "@vkontakte/vkui";

const ModalBuyTransfer = ({
  setActiveModal,
  openPaymentSystem,
  onChangeHandlerPayment,
  onChangeHandlerAmount,
  type,
  onChangeHandlerId,
  transfer

}) => {
  return (
    <>
      {type === "buy" ? (
        <ModalRoot activeModal="buy-durkacoins">
          <ModalCard
            id="buy-durkacoins"
            onClose={() => setActiveModal(null)}
            icon={
              <Avatar
                size={72}
                src="https://sun9-50.userapi.com/c857036/v857036390/aa9a0/i-iQZ4973FQ.jpg"
              />
            }
            header="Купить DurkaCoins"
            caption={
              <>
                <FormLayout>
                  <Input
                    top="Укажите кол-во DuraCoins"
                    placeholder="5"
                    onChange={onChangeHandlerPayment}
                    type="number"
                  />
                </FormLayout>
                <InfoRow id="toPay">К оплате: 0 руб</InfoRow>
              </>
            }
            actions={[
              {
                title: "Купить",
                mode: "commerce",
                onChange: { onChangeHandlerAmount },

                action: () => {
                  openPaymentSystem();
                },
              },
            ]}
          ></ModalCard>
        </ModalRoot>
      ) : (
        <ModalRoot activeModal="transfer">
          <ModalCard
            id="transfer"
            onClose={() => setActiveModal(null)}
            icon={
              <Avatar
                size={72}
                src="https://sun9-50.userapi.com/c857036/v857036390/aa9a0/i-iQZ4973FQ.jpg"
              />
            }
            header="Перевести DurkaCoins"
            caption={
              <>
                <FormLayout>
                  <Input
                    top="Введите ссылку на получателя"
                    placeholder="https://vk.com/durkacoin"
                    onChange={onChangeHandlerId}
                    type="text"
                  />
                  <FormLayoutGroup top="Укажите кол-во DurkaCoins">
                    <Input
                      onChange={onChangeHandlerAmount}
                      placeholder="10"
                      type="number"
                    />
                  </FormLayoutGroup>
                </FormLayout>
              </>
            }
            actions={[
              {
                title: "Перевести",
                mode: "commerce",
                action: () => {
                  transfer();
                },
              },
            ]}
          ></ModalCard>
        </ModalRoot>
      )}
    </>
  );
};

export { ModalBuyTransfer };
