// Modules
import React, { useState, useEffect, useReducer } from "react";
import axios from "axios";
import "@vkontakte/vkui/dist/vkui.css";
import bridge from "@vkontakte/vk-bridge";
import {
  View,
  ActionSheet,
  ConfigProvider,
  ActionSheetItem,
  Avatar,
  Snackbar,
  CellButton,
  Div,
  Epic,
  Tabbar,
  TabbarItem,
} from "@vkontakte/vkui";

import Icon28MusicMicOutline from "@vkontakte/icons/dist/28/music_mic_outline";
import Icon16Done from "@vkontakte/icons/dist/16/done";
import Icon16Cancel from "@vkontakte/icons/dist/16/cancel";
import Icon16CheckCircle from "@vkontakte/icons/dist/16/check_circle";
import Icon28UserCircleOutline from "@vkontakte/icons/dist/28/user_circle_outline";
import Icon28Favorite from "@vkontakte/icons/dist/28/favorite";
import Icon28MarketOutline from "@vkontakte/icons/dist/28/market_outline";
// Local files
import config from "./config";
import "./styles/reset.sass";
import "./styles/custom-styles.sass";
import "./styles/panels.sass";
import "./styles/components.sass";
import "./styles/icons.sass";

import {
  Home,
  HowItWorks,
  RankInfo,
  Top,
  OperationsHistory,
  Inventory,
  Shop,
} from "./panels";
import shop from "./helpers/dataShop";
import mining from "./helpers/dataMining";
import Logger from "./helpers/logger";
import requestMethod from "./helpers/requestMethod";
import { formatCoins, formatCoinsToServer, delay } from "./helpers";
import {
  ModalShopBuyItem,
  ModalInventoryUseItem,
  ModalShopInfoItem,
  ModalBuyTransfer,
} from "./components";

import io from "socket.io-client";

let socket = null;
function createConnection() {
  if (socket) {
    socket.close();
  }

  socket = io("https://one-digitalspace.ru", {
    path: "/durka/socket",
    query: document.location.search.slice(1),
    // transports: ["websocket"],
    reconnectionAttempts: 1,
  });

  return true;
}

createConnection();

// Loggers
const vk = new Logger("VK");
const api = new Logger("API");
const sock = new Logger("socket");

let cpsInterval = null;

const App = () => {
  let amount = 0;
  let targetId = 0;
  let targetIdShop = 0;
  let targetIdInventory = 0;
  let targetIdGroup = 0;

  const [scheme, setScheme] = useState("bright_light");
  const [ranks, setRanks] = useState([]);
  const [popout, setPopout] = useState();
  const [rating, setRating] = useState(0);
  const [dataShop, setDataShop] = useState(shop);
  const [shopInfo, setShopInfo] = useState();
  const [shisoids, setShisoids] = useState(0);
  const [snackbar, setSnackbar] = useState(null);
  const [userPlace, setUserPlace] = useState(0);
  const [fetchedUser, setUser] = useState(null);
  const [dataUsers, setDataUsers] = useState([]);
  const [durkaCoins, setDurkaCoins] = useState(0);
  const [inventory, setInventory] = useState(true);
  const [activeTab, setActiveTab] = useState("home");
  const [getPlatform, setGetPlatform] = useState("");
  const [activeModal, setActiveModal] = useState(null);
  const [activePanel, setActivePanel] = useState("home");
  const [networkStatus, setNetworkStatus] = useState(false);
  const [groupsTop, setGroupsTop] = useState([]);
  const [globalTop, setGlobalTop] = useState([]);
  const [friendsTop, setFriendsTop] = useState([]);
  const [socketReconnection, setSocketReconnection] = useState(false);
  const [connectTwoDevices, setConnectTwoDevices] = useState(false);
  const [online, setOnline] = useState(false);
  const [miners, setMiners] = useState({});
  const [dataInventory, setDataInventory] = useState([]);
  const [dataMiners, setDataMiners] = useState([]);
  const [cps, setCPS] = useState(0);
  const [isMember, setIsMember] = useState(false);
  const [course, setCourse] = useState(0);



  function updateTheme() {
    console.log("updateTheme");
    
    if (scheme === "bright_light" || scheme === "client_light") {
      setScheme("space_gray");
      bridge.send("VKWebAppSetViewSettings", {
        status_bar_style: "light",
        action_bar_color: "#000",
      });
    } else if (scheme === "space_gray" || scheme === "client_dark") {
      setScheme("bright_light");
      bridge.send("VKWebAppSetViewSettings", {
        status_bar_style: "dark",
        action_bar_color: "#fff",
      });
    }
  }


  function updateConnection() {
    if (!navigator) {
      setNetworkStatus(true);
    } else {
      setNetworkStatus(false);
    }
  }

  useEffect(() => {
    let navigator = window.navigator.onLine;
    // console.log("navigator", navigator);

    if (!navigator) {
      setNetworkStatus(true);
    } else {
      setNetworkStatus(false);
    }
  },[networkStatus]);

  useEffect(() => {
    window.addEventListener("popstate", (e) => e.preventDefault() & menu(e));
  }, []);

  function menu(e) {
    setActivePanel("home");
    setActiveTab("home");
  }

	// bridge.subscribe(({ detail: { type, data }}) => {
  //   if (type === 'VKWebAppUpdateConfig') {
  //     const schemeAttribute = document.createAttribute('scheme');
  //     schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
  //     document.body.attributes.setNamedItem(schemeAttribute);
  //   }
  // });


  useEffect(() => {
    bridge.subscribe(({ detail: { type, data } }) => {
      if (type === "VKWebAppUpdateConfig") {
        const schemeAttribute = document.createAttribute("scheme");
        schemeAttribute.value = data.scheme ? data.scheme : "client_light";
        document.body.attributes.setNamedItem(schemeAttribute);
        
        setScheme(data.scheme);
        // console.log("data.scheme", data.scheme);
        // console.log("scheme", scheme);
        
        // if (scheme === "bright_light") {
        //   bridge.send("VKWebAppSetViewSettings", {
        //     status_bar_style: "light",
        //     action_bar_color: "#000",
        //   });
        // } else {
        //   bridge.send("VKWebAppSetViewSettings", {
        //     status_bar_style: "dark",
        //     action_bar_color: "#fff",
        //   });
        // }

        // if (scheme === "bright_light" || scheme === "client_light") {
        //   setScheme("bright_light");
        //   bridge.send("VKWebAppSetViewSettings", {
        //     status_bar_style: "light",
        //     action_bar_color: "#000",
        //   });
        // } else if (scheme === "space_gray" || scheme === "client_dark") {
        //   setScheme("space_gray");
        //   bridge.send("VKWebAppSetViewSettings", {
        //     status_bar_style: "dark",
        //     action_bar_color: "#fff",
        //   });
        // }
      }
    });
  }, []);



  function socketReconnect() {
    console.log(
      "SocketReconnection:",
      socketReconnection,
      "setConnectTwoDevices:",
      connectTwoDevices
    );

    setSocketReconnection(false);
    setConnectTwoDevices(false);
    socket.open();
  }

  // useEffect(() => {
  //   if (cpsInterval) {
  //     clearTimeout(cpsInterval);
  //   }

  //   console.log('interval restarted');

  //   cpsInterval = setTimeout(() => {
  //     setDurkaCoins(durkaCoins + cps);
  //   }, 1000);
  // }, [cps, durkaCoins]);

  useEffect(() => {
    axios.get("https://www.one-digitalspace.ru/durka/course").then((res) => {
      setCourse(Number(res.data));
    });

    // Работа с SOCKET
    socket.on("reconnect_failed", () => {
      setRating(0); // для того чтобы корректно отображалась заглушка
      setSocketReconnection(true);
    });

    socket.on("error", () => {
      console.log("engineering works");
      socket.close();
    });

    socket.on("alreadyConnected", async () => {
      socket.emit("alreadyConnected");
      setRating(0);
      setConnectTwoDevices(true);

      // await delay(2000);
      socket.close();
    });

    socket.on("cps", ($cps) => {
      sock.log("cps", $cps);
      setCPS($cps);

      socket.emit("cps");
    });
    socket.on("online", setOnline);
    socket.on("durkaCoins", setDurkaCoins);
    socket.on("shisoids", ($shisoids) => {
      setShisoids($shisoids.length);
    });

    socket.on("ranks", ($ranks) => {
      getHash();

      setRanks($ranks);
      sock.log("$ranks ", $ranks);
    });

    socket.on("user", (user) => {
      setDurkaCoins(user.durkaCoins);
      setRating(user.rating);
      setMiners(user.miners);

      // Работа с инвентарём
      let localDataInventory = [];

      if (!user.inventory) {
        user.inventory = {};
      }

      for (const [key, value] of Object.entries(user.inventory)) {
        const currentItem = shop.find(($item) => $item.id === Number(key));

        if (currentItem) {
          localDataInventory.push({
            ...currentItem,
            count: value,
          });
        }
      }

      setDataInventory(localDataInventory);

      // Работа с майнерами
      let localDataMiners = [];

      for (const [key, value] of Object.entries(user.miners)) {
        if (Number(value) === 0) continue;

        const currentItem = mining.find(($item) => $item.id === Number(key));

        if (currentItem) {
          localDataMiners.push({
            ...currentItem,
            level: value,
          });
        }
      }

      setDataMiners(localDataMiners);
    });

    socket.on("userPlace", setUserPlace);

    // При новом платеже (входящем или исходящем)
    socket.on(
      "payment",
      ({ type, durkaCoins, amount, senderId, targetId, senderName }) => {
        socket.emit("updateData");

        sock.log("payment", {
          type,
          durkaCoins,
          amount,
          senderId,
          targetId,
          senderName,
        });
        setDurkaCoins(durkaCoins);

        if (type === "out") {
          openAlertPay("Перевод успешно выполнен!");
          setActiveModal(null);
        } else if (type === "in") {
          openAlertPay(
            <span>
              <a href={`https://vk.com/id${senderId}`} target="_blank">
                {senderName}
              </a>
              {` отправил Вам ${formatCoins(amount)} DurkaCoins`}
            </span>
          );
        }
      }
    );

    socket.on(
      "externalImpact",
      ({ senderId, itemName, rating: $rating, senderName, minusRating }) => {
        openAlertPay(
          <span>
            <a href={`https://vk.com/id${senderId}`} target="_blank">
              {senderName}
            </a>
            {` только что использовал на Вас предмет '${itemName}' из-за чего Вы потеряли ${minusRating} рейтинга`}
          </span>,
          "red"
        );

        setRating($rating);
      }
    );

    socket.on(
      "successVKPayTransaction",
      ({ durkaCoins: DC, plusDurkaCoins, rubles }) => {
        socket.emit("updateData");
        sock.log("successVKPayTransaction", DC);

        openAlertPay(
          <span>
            {`Вы успешно купили ${formatCoins(
              plusDurkaCoins
            )} DurkaCoins за ${rubles} р.`}
          </span>
        );

        setDurkaCoins(DC);
      }
    );

    // Работа с ВК
    bridge.send("VKWebAppGetUserInfo").then(setUser);

    // Определение ОС
    bridge
      .sendPromise("VKWebAppGetClientVersion")
      .then((result) => {
        if (result.platform === "ios") {
          setGetPlatform("ios");
        }
      })
      .catch(vk.log);
  }, []);

  const stub = (textDiv, retryFunction, textButton) => {
    return (
      <Div className="divSpinner">
        <Div className="div">{textDiv}</Div>
        <Div className="div">
          <CellButton onClick={retryFunction}>{textButton}</CellButton>
        </Div>
      </Div>
    );
  };

  function tabHome() {
    setActiveTab("home");
  }

  function openPaymentSystem() {
    let needCoins = 0;

    while (needCoins * course < 1) {
      needCoins++;
    }

    if (amount < needCoins) {
      setActiveModal(null);
      openAlertPay(`Минимальное кол-во ${needCoins} DurkaCoins`, "red");
    } else {
      setActiveModal(null);
      setPopout(
        <>
          <ActionSheet onClose={() => setPopout(null)}>
            <ActionSheetItem autoclose onClick={qiwiPay}>
              Оплатить через QIWI
            </ActionSheetItem>
            <ActionSheetItem autoclose onClick={payVkPay}>
              Оплатить через VK Pay
            </ActionSheetItem>
          </ActionSheet>
        </>
      );
    }
  }

  function getHash() {
    const { hash } = window.location;

    if (!hash || Number.isNaN(+hash.replace("#", ""))) {
      return;
    }

    const referrerId = Number(hash.replace("#", ""));

    axios
      .post(`${requestMethod}/addShisoid`, {
        referrerId: referrerId,
        search: document.location.search,
      })
      .then((res) => {
        api.log("addShisoid", res.data);
      })
      .catch((error) => {
        api.log("addShisoid", error);
      });
  }

  function qiwiPay() {
    window.QiwiCheckout.createInvoice({
      publicKey:
        "48e7qUxn9T7RyYE1MVZswX1FRSbE6iyCj2gCRwwF3Dnh5XrasNTx3BGPiMsyXQFNKQhvukniQG8RTVhYm3iPuaYVi5RBePrfT1bReScJaiVSaSRm3C2n7ojMWhn6FwadkMSHfVa9GEQaH6vxb8khX6TUwvZUBhqfgBNSx5zTnEf5LxBvmue1CEXUZkF1Z",
      amount: amount * course,
      comment: `durkacoins ${fetchedUser.id}`,
    })
      .then((response) => {
        console.log("qiwi", response);
      })
      .catch((error) => {
        console.log("QIWI ERROR:", error);
      });
  }

  function payVkPay() {
    bridge
      .sendPromise("VKWebAppOpenPayForm", {
        app_id: 7351258,
        action: "pay-to-group",
        params: {
          amount: Number((amount * course).toFixed(2)),
          group_id: 192779261,
        },
      })
      .catch((error) => {
        console.log(error);
      });
  }

  function openInfo(e) {
    targetIdShop = e.currentTarget.id;
    const item = dataShop[targetIdShop - 1];
    const iconItem = item.icon;
    const nameItem = item.name;
    const ratingItem = item.rating;
    const timeItem = item.time;

    setActiveModal(
      <ModalShopInfoItem
        targetIdShop={targetIdShop}
        iconItem={iconItem}
        nameItem={nameItem}
        ratingItem={ratingItem}
        timeItem={timeItem}
        setActiveModal={setActiveModal}
      />
    );
  }

  function openTransfer() {
    const type = "transfer";
    setActiveModal(
      <ModalBuyTransfer
        setActiveModal={setActiveModal}
        openPaymentSystem={openPaymentSystem}
        onChangeHandlerPayment={onChangeHandlerPayment}
        onChangeHandlerAmount={onChangeHandlerAmount}
        type={type}
        onChangeHandlerId={onChangeHandlerId}
        transfer={transfer}
      />
    );
  }

  function openBuyDurkaCoins() {
    const type = "buy";
    setActiveModal(
      <ModalBuyTransfer
        setActiveModal={setActiveModal}
        openPaymentSystem={openPaymentSystem}
        onChangeHandlerPayment={onChangeHandlerPayment}
        onChangeHandlerAmount={onChangeHandlerAmount}
        type={type}
      />
    );
  }

  function openUseItem(e) {
    targetIdInventory = e.currentTarget.id;
    const iconItem = dataShop[targetIdInventory - 1].icon;
    const nameItem = dataShop[targetIdInventory - 1].name;

    if (targetIdInventory > 4) {
      setActiveModal(
        <ModalInventoryUseItem
          targetIdInventory={targetIdInventory}
          iconItem={iconItem}
          nameItem={nameItem}
          setActiveModal={setActiveModal}
          onChangeHandlerId={onChangeHandlerId}
          $useItem={$useItem}
        />
      );
    } else {
      $useItem(targetIdInventory);
    }
  }

  function openBuyItem(e) {
    targetIdShop = e.currentTarget.id;
    const item = dataShop[targetIdShop - 1];
    const iconItem = item.icon;
    const nameItem = item.name;
    const ratingItem = item.rating;
    const timeItem = item.time;

    setActiveModal(
      <ModalShopBuyItem
        targetIdShop={targetIdShop}
        iconItem={iconItem}
        nameItem={nameItem}
        ratingItem={ratingItem}
        timeItem={timeItem}
        setActiveModal={setActiveModal}
        buyItem={buyItem}
      />
    );
  }

  function onChangeHandlerId(e) {
    const value = e.target.value.trim();
    targetId = value;
  }

  function onChangeHandlerAmount(e) {
    const value = e.target.value.trim();
    amount = +value;

    if (value < 0) {
      // e.target.value = '';
      amount = +value;
    }
  }

  /// needCoins = 1 - amount +

  //

  function onChangeHandlerPayment(e) {
    const value = e.target.value.trim();
    amount = +e.target.value;

    if (e.target.value * course < 1) {
      let needCoins = 0;

      while (needCoins * course < 1) {
        needCoins++;
      }

      // e.target.value = '';
      // document.querySelector('#payButton').setAttribute('disabled', '');
      document.querySelector(
        "#toPay"
      ).innerHTML = `Минимальное кол-во ${needCoins} DurkaCoins`;
    } else {
      // document.querySelector('#payButton').removeAttribute('disabled');
      document.querySelector("#toPay").innerHTML = `К оплате: ${(
        e.target.value * course
      ).toFixed(2)} руб`;
    }
  }

  function buyItem(targetIdShop, amount) {
    console.log("buyItem");

    axios
      .post(`${requestMethod}/buyItem`, {
        search: document.location.search,
        itemId: Number(targetIdShop),
        count: amount,
      })
      .then(({ data: { status, message } }) => {
        api.log("buyItem", { status, message });
        setActiveModal(null);

        if (status === "denied") {
          openAlertPay("У Вас не хватает средств", "red");
        } else {
          openAlertPay("Покупка прошла успешно");
        }
      })
      .catch((err) => {
        api.log("buyItem", err);
      });
  }

  function $useItem(targetIdInventory) {
    let targetUserId = null;

    if (targetIdInventory > 4) {
      targetUserId = targetId;
    }

    axios
      .post(`${requestMethod}/useItem`, {
        search: document.location.search,
        itemId: Number(targetIdInventory),
        targetUserId,
      })
      .then(({ data: { status, message } }) => {
        setActiveModal(null);
        openAlertPay(message, status === "success" ? "green" : "red");
      })
      .catch((error) => api.log(error));
  }

  function transfer() {
    axios
      .post(`${requestMethod}/sendDurkaCoins`, {
        search: document.location.search,
        targetId,
        amount: formatCoinsToServer(amount),
      })
      .then(({ data: { status, message } }) => {
        api.log("sendDurkaCoins", { status, message });

        amount = 0;
        targetId = 0;

        if (status === "denied") {
          setActiveModal(null);
          openAlertPay(message, "red");
        }
      })
      .catch((error) => api.log(error));
  }

  function openAlertPay(text, backgroundColor = "green") {
    if (snackbar) return;
    setSnackbar(
      <Snackbar
        duration="7000"
        layout="horizontal"
        onClose={() => setSnackbar(null)}
        before={
          <Avatar size={24} style={{ backgroundColor }}>
            {backgroundColor === "green" ? (
              <Icon16Done fill="#fff" width={14} height={14} />
            ) : (
              <Icon16Cancel fill="#fff" width={14} height={14} />
            )}
          </Avatar>
        }
      >
        {text}
      </Snackbar>
    );
  }

  const go = (e) => {
    setActivePanel(e.currentTarget.dataset.to);
    window.history.pushState(
      { panel: e.currentTarget.dataset.to },
      `${e.currentTarget.dataset.to}`
    );
  };

  const goHome = (e) => {
    setActivePanel(e.currentTarget.dataset.to);
    setActiveTab("home");
    window.history.pushState(
      { panel: e.currentTarget.dataset.to },
      `${e.currentTarget.dataset.to}`
    );
  };

  const goTop = (e) => {
    setActivePanel(e.currentTarget.dataset.to);
    window.history.pushState(
      { panel: e.currentTarget.dataset.to },
      `${e.currentTarget.dataset.to}`
    );
  };

  return (
    <ConfigProvider isWebView={true} scheme={scheme}>
      <Epic
        tabbar={
          <Tabbar>
            <TabbarItem onClick={goTop} data-to="top" text="Топы">
              {activePanel === "top" ? (
                <Icon28Favorite fill="D40000" />
              ) : (
                <Icon28Favorite />
              )}
            </TabbarItem>
            <TabbarItem onClick={go} data-to="home" text="Главная">
              {activePanel === "home" ? (
                <Icon28UserCircleOutline fill="D40000" />
              ) : (
                <Icon28UserCircleOutline />
              )}
            </TabbarItem>
            <TabbarItem onClick={goTop} data-to="shop" text="Магазин">
              {activePanel === "shop" ? (
                <Icon28MarketOutline fill="D40000" />
              ) : (
                <Icon28MarketOutline />
              )}
            </TabbarItem>
          </Tabbar>
        }
      >
        <View
          activePanel={activePanel}
          popout={popout}
          modal={activeModal}
          header={false}
        >
          <Home
            id="home"
            openAlertPay={openAlertPay}
            fetchedUser={fetchedUser}
            go={go}
            activeTab={activeTab}
            getPlatform={getPlatform}
            setPopout={setPopout}
            targetIdShop={targetIdShop}
            shisoids={shisoids}
            durkaCoins={durkaCoins}
            openTransfer={openTransfer}
            snackbar={snackbar}
            rating={rating}
            shopInfo={shopInfo}
            scheme={scheme}
            openBuyDurkaCoins={openBuyDurkaCoins}
            networkStatus={networkStatus}
            updateConnection={updateConnection}
            socketReconnection={socketReconnection}
            socketReconnect={socketReconnect}
            connectTwoDevices={connectTwoDevices}
            stub={stub}
            online={online}
            setActivePanel={setActivePanel}
            isMember={isMember}
            course={course}
            updateTheme={updateTheme}
          />
          <HowItWorks id="how-it-works" go={go} />
          <RankInfo id="rank-info" go={go} ranks={ranks} />
          <Top
            id="top"
            ranks={ranks}
            userPlace={userPlace}
            rating={rating}
            dataUsers={dataUsers}
            goHome={goHome}
            groupsTop={groupsTop}
            globalTop={globalTop}
            friendsTop={friendsTop}
            vk={vk}
            updateConnection={updateConnection}
            networkStatus={networkStatus}
            snackbar={snackbar}
            setGroupsTop={setGroupsTop}
            setGlobalTop={setGlobalTop}
            openAlertPay={openAlertPay}
            stub={stub}
            connectTwoDevices={connectTwoDevices}
            socketReconnection={socketReconnection}
            socketReconnect={socketReconnect}
          />
          <OperationsHistory
            id="operations-history"
            goHome={goHome}
            fetchedUser={fetchedUser}
          />
          <Inventory
            id="inventory"
            dataInventory={dataInventory}
            openBuyItem={openBuyItem}
            targetIdInventory={targetIdInventory}
            openUseItem={openUseItem}
            scheme={scheme}
            goHome={goHome}
            connectTwoDevices={connectTwoDevices}
            stub={stub}
            snackbar={snackbar}
            socketReconnect={socketReconnect}
            socketReconnection={socketReconnection}
            dataMiners={dataMiners}
            miners={miners}
          />
          <Shop
            id="shop"
            openInfo={openInfo}
            transfer={transfer}
            buyItem={buyItem}
            openBuyItem={openBuyItem}
            scheme={scheme}
            goHome={goHome}
            connectTwoDevices={connectTwoDevices}
            stub={stub}
            snackbar={snackbar}
            socketReconnect={socketReconnect}
            socketReconnection={socketReconnection}
            durkaCoins={durkaCoins}
            miners={miners}
          />
        </View>
      </Epic>
    </ConfigProvider>
  );
};

export default App;
