import React from 'react';
import { useState, useEffect } from 'react';
import {
    Panel, PanelHeaderSimple, PanelHeaderBack,
    FixedLayout, Tabs, TabsItem, Div, Spinner,
} from '@vkontakte/vkui';

import {InventoryItem, MiningInventoryItem } from '../components';
import dataMining from '../helpers/dataMining';

const Inventory = ({ id, dataInventory,
    targetIdInventory, openUseItem, scheme, goHome, connectTwoDevices, stub, socketReconnect, networkStatus,
    openBuyItem, updateConnection, socketReconnection, snackbar, dataMiners, miners }) => {

    const [activeTab, setActiveTab] = useState('inventory');


    // useEffect(() => {
    //     getUsers();
    //     getGroups();
    // }, []);

    function tabInventory() {
        setActiveTab('inventory');
        window.scrollTo(0, 0);
    }

    function tabMaining() {
        setActiveTab('maining');
        window.scrollTo(0, 0);
    }

    function tabPromo() {
        setActiveTab('promo');
        window.scrollTo(0, 0);
    }

    const renderInventory = dataInventory.map((item, index) => {
        return (
            <InventoryItem
                key={index}
                id={item.id}
                name={item.name}
                icon={item.icon}
                count={item.count}
                openBuyItem={openBuyItem}
                targetIdInventory={targetIdInventory}
                openUseItem={openUseItem}
                scheme={scheme}
            />
        );
    });


    const renderMaining = dataMiners.map((item, index) => {
        const currentItem = dataMining.find(($item) => (
            $item.id === item.id
        )).levels[item.level - 1];

        return (
            <MiningInventoryItem
                key={index}
                id={item.id}
                name={item.name}
                icon={item.icon}
                cps={currentItem.cps}
                scheme={scheme}
                level={item.level}
            />
        );
    });

    return (
        <Panel id={id} separator={false}>
            <PanelHeaderSimple left={<PanelHeaderBack onClick={goHome} data-to="home" />}>
                Инвентарь
            </PanelHeaderSimple>


            {connectTwoDevices
                ?
                stub('Вы подключились с 2 устройств ', socketReconnect, "Переподключиться")
                :
                <>
                    <FixedLayout vertical="top" filled={true}>
                        <Tabs >
                            <TabsItem
                                onClick={tabInventory}
                                selected={activeTab === 'inventory'}
                            >
                                Имущество
                           </TabsItem>
                            <TabsItem
                                onClick={tabMaining}
                                selected={activeTab === 'maining'}
                            >
                                Майнеры
                            </TabsItem>
                        </Tabs>
                    </FixedLayout>


                    {networkStatus
                        ?
                        stub('Отсутствует интернет - соединение!', updateConnection, "Повторить")
                        :
                        <>
                            {socketReconnection
                                ?
                                stub('Ошибка подключения к серверу', socketReconnect, "Повторить")
                                :
                                <>
                                    {activeTab === 'inventory'
                                        ?
                                        <>
                                            {renderInventory.length
                                                ?
                                                <Div style={{ marginTop: 55}}>
                                                    {renderInventory}
                                                </Div>
                                                :
                                                <div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }}>
                                                    <Spinner size="large" style={{ marginTop: 90 }} />
                                                </div>
                                            }
                                        </>
                                        :
                                        ''
                                    }


                                    {activeTab === 'maining'
                                        ?
                                        <>
                                            {renderMaining.length
                                                ?
                                                <Div style={{ marginTop: 55}}>
                                                    {renderMaining}
                                                </Div>
                                                :
                                                <div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }}>
                                                    <Spinner size="large" style={{ marginTop: 90 }} />
                                                </div>
                                            }
                                        </>
                                        :
                                        ''
                                    }

                                </>
                            }

                            {snackbar}
                        </>
                    }
                </>
            }
        </Panel >
    );
};

export { Inventory };
