import React from "react";
import { useState, useEffect } from "react";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  FixedLayout,
  Tabs,
  TabsItem,
  Div,
  Spinner,
} from "@vkontakte/vkui";

import { ShopItem, MiningItem } from "../components";
import { formatCoins } from "../helpers";
import shop from "../helpers/dataShop";
import mining from "../helpers/dataMining";

const Shop = ({
  id,
  openInfo,
  transfer,
  buyItem,
  scheme,
  goHome,
  connectTwoDevices,
  stub,
  socketReconnect,
  networkStatus,
  openBuyItem,
  updateConnection,
  socketReconnection,
  snackbar,
  durkaCoins,
  miners
}) => {

  const [activeTab, setActiveTab] = useState("product");
  // const [localScheme, setLocalScheme] = useState("bright_light");

  // useEffect(() => {
  //   console.log("scheme", scheme);
  //   if (scheme) {
  //     setLocalScheme(scheme);
  //     console.log("localScheme", localScheme);
      
  //   }
  // }, [scheme]);



  function tabProduct() {
    setActiveTab("product");
    window.scrollTo(0, 0);
  }

  function tabMining() {
    setActiveTab("mining");
    window.scrollTo(0, 0);
  }

  const renderShop = shop.map((item, index) => {
    return (
      <ShopItem
        key={index}
        id={item.id}
        name={item.name}
        price={item.price}
        icon={item.icon}
        openInfo={openInfo}
        openBuyItem={openBuyItem}
        scheme={scheme}
      />
    );
  });

  const renderMining = mining.map((item, index) => {
    let currentLevelItem = item.levels[miners[item.id]];
    let localBuyItem = buyItem;

    if (miners[item.id] >= 5) {
      currentLevelItem = item.levels[miners[item.id] - 1];
      // currentLevelItem = "Максимальный уровень";
      localBuyItem = () => {};

    }

    return (
      <MiningItem
        key={index}
        id={item.id}
        name={item.name}
        price={currentLevelItem.price}
        icon={item.icon}
        cps={currentLevelItem.cps}
        scheme={scheme}
        buyItem={localBuyItem}
        level={miners[item.id]}
      />
    );
  });

  return (
    <Panel id={id} separator={false}>
      <PanelHeaderSimple left={<PanelHeaderBack onClick={goHome} data-to="home" />}>
        Магазин
      </PanelHeaderSimple>

      {connectTwoDevices ? (
        stub(
          "Вы подключились с 2 устройств ",
          socketReconnect,
          "Переподключиться"
        )
      ) : (
        <>
          <FixedLayout vertical="top" filled={true}>
            <Tabs>
              <TabsItem onClick={tabProduct} selected={activeTab === "product"}>
                Товары
              </TabsItem>
              <TabsItem onClick={tabMining} selected={activeTab === "mining"}>
                Майнеры
              </TabsItem>
            </Tabs>
          </FixedLayout>

          {networkStatus ? (
            stub(
              "Отсутствует интернет - соединение!",
              updateConnection,
              "Повторить"
            )
          ) : (
            <>
              {socketReconnection ? (
                stub(
                  "Ошибка подключения к серверу",
                  socketReconnect,
                  "Повторить"
                )
              ) : (
                <>
                  {activeTab === "product" ? (
                    <>
                      {renderShop.length ? (
                        <Div style={{ marginTop: 55 }}>
                          <Div
                            style={{
                              display: "flex",
                              alignItems: "center",
                              flexDirection: "row",
                              marginLeft: 25,
                              marginBottom: 10,
                            }}
                          >
                            Баланс: {formatCoins(durkaCoins)}
                            <Div className="iconDC" style={{ marginLeft: 8 }} />
                          </Div>

                          {renderShop}
                        </Div>
                      ) : (
                        <div
                          style={{
                            display: "flex",
                            alignItems: "center",
                            flexDirection: "column",
                          }}
                        >
                          <Spinner size="large" style={{ marginTop: 90 }} />
                        </div>
                      )}
                    </>
                  ) : (
                    ""
                  )}

                  {activeTab === "mining" ? (
                    <>
                      {renderMining.length ? (
                        <Div style={{ marginTop: 55 }}>
                          <Div
                            style={{
                              display: "flex",
                              alignItems: "center",
                              flexDirection: "row",
                              marginLeft: 25,
                              marginBottom: 10,
                            }}
                          >
                            Баланс: {formatCoins(durkaCoins)}
                            <Div className="iconDC" style={{ marginLeft: 8 }} />
                          </Div>

                          {renderMining}
                        </Div>
                      ) : (
                        <div
                          style={{
                            display: "flex",
                            alignItems: "center",
                            flexDirection: "column",
                          }}
                        >
                          <Spinner size="large" style={{ marginTop: 90 }} />
                        </div>
                      )}
                    </>
                  ) : (
                    ""
                  )}
                </>
              )}
              {snackbar}
            </>
          )}
        </>
      )}
    </Panel>
  );
};

export { Shop };
